.card__title {
  margin: 0; /* Сброс стандартных стилей */
  color: #000000;
  min-height: 29px;
  font-style: normal;
  font-weight: 900;
  font-size: 24px;
  line-height: 29px;
  /* При переполнении текст обрезается */
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  padding-right: 20px;
}
